nav{
    background-color: white;
    position: relative;
    top: 30px;
}

.navbar-nav{
    width: 100%;  
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;       
}

#titlenav{
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;
    color: #1a3c34;  
    font-size: 35px;  
    float: left;
    position: relative;
    left: 100px;
    top: 1px; 
}

.nav-item{
    width: 160px;
    float: right;
    position: relative;
    left: 300px;
    color: #007485;
    font-size: 25px;
}

#navbarDropdown{
    color: #1a3c34;  
}

#homenav{
    width: 175px;
    float: right;
    position: relative;
    left: 350px;
    top: 8px;    
    font-size: 25px;
    color: #1a3c34;
}

#portfolionav{
    width: 275px;
    color: #007485;
}

#signoutnav{
    float: right;
    position: relative;
    right: 125px;
    top: 1px;
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;  
}


